footer {
    margin-top: 10vmax;
    padding: 2vmax;
    background-color: #F7AD64;
    color: rgb(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
}

.leftFooter {
    width: 20%;
    display: flex;

    flex-direction: column;
    align-items: center;
}

.leftFooter>h4 {
    font-family: 'Poppins', sans-serif;
    font-size: 1vmax;
}

.leftFooter>p {
    text-align: center;
    font-size: 1.2vmax;
    font-family: 'Poppins', sans-serif;
}

.leftFooter>img {
    width: 10vmax;
    margin: 1vmax;
    cursor: pointer;
}

.midFooter {
    width: 60%;

    text-align: center;
}

.midFooter>h1 {
    font-size: 4vmax;
    font-family: 'Dancing Script', cursive;
    color: #eb4034;
}

.midFooter>p {
    max-width: 60%;
    font-family: 'Poppins', sans-serif;
    margin: 1vmax auto;
}

.rightFooter {
    width: 20%;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.rightFooter>h4 {
    font-family: 'Poppins', sans-serif;
    font-size: 1.4vmax;
    text-decoration: underline;
}

.rightFooter>a {
    text-decoration: none;
    font-size: 1.3vmax;
    font-family: 'Poppins', sans-serif;
    color: rgb(0, 0, 0, 0.7);
    transition: all 0.5s;
    margin: 0.5vmax;
}

.rightFooter>a:hover {
    color: #eb4034;
}